import React, { useState } from "react";
import { toast } from "react-hot-toast";
import CopyToClipboard from "react-copy-to-clipboard";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { motion } from "framer-motion/dist/framer-motion";
import { faCircleXmark } from "@fortawesome/free-solid-svg-icons";

const correos = [
  {
    id: "copyJefe1",
    text: "Asunto: Propuesta de prueba de concepto para demostrar los beneficios de los robots RPA+IA\n\nEstimado [Nombre del jefe],\n\nEntiendo que puede haber ciertas dudas sobre si los robots RPA+IA son efectivos en nuestra rea de trabajo. Sin embargo, me gustara proponerle una prueba de concepto que le permitir experimentar de primera mano cmo la implementacin de esta tecnologa puede transformar la eficiencia de nuestra organizacin.\n\nHe contactado a un desarrollador externo que es experto en la implementacin de robots RPA+IA en empresas de nuestro sector. l estara encantado de trabajar con nosotros para demostrar los beneficios de esta tecnologa en nuestra rea de trabajo.\n\nDurante esta prueba de concepto, un desarrollador experto  har unas pruebas demostrativas de  cmo los robots pueden realizar esas tareas de manera ms rpida y precisa que los humanos. Creo que esta experiencia ser muy valiosa para entender cmo la implementacin de robots RPA+IA puede mejorar significativamente la eficiencia en nuestra organizacin.\n\nPor favor, hgamelo saber si est interesado en esta propuesta. Si lo est, estar encantado de programar una reunin para discutir los detalles y establecer un plan de accin.\n\nAgradezco su consideracin.\n\nSaludos cordiales,\n\n[Nombre del remitente]",
    mostrarMas: false,
  },
  {
    id: "copyJefe2",
    text: "Asunto: Organizacin de la prueba de concepto para la implementacin de robots RPA+IA\n\nEstimado [Nombre del jefe],\n\nLe escribo para proponerle una prueba de concepto que transformar la eficiencia de nuestra organizacin mediante la implementacin de robots RPA+IA. Como sabemos, la automatizacin de tareas repetitivas puede ayudar a mejorar la eficiencia, la productividad y la precisin en nuestra rea de trabajo.\n\nPara demostrar cmo los robots pueden ser beneficiosos en nuestra empresa, he contactado a un desarrollador externo experto en la implementacin de robots RPA+IA en empresas de nuestro sector. l estar encantado de trabajar con nosotros en una prueba de concepto que nos permitir experimentar de primera mano los beneficios que los robots pueden aportar a nuestra empresa.\n\nSi est interesado en esta propuesta, me encantara programar una reunin para discutir los detalles y establecer un plan de accin.\n\nAgradezco su consideracin y espero su respuesta.\n\nSaludos cordiales,\n\n[Nombre del remitente]",
    mostrarMas: false,
  },
  {
    id: "copyJefe3",
    text: "Asunto: Aumente la eficiencia de nuestra organizacin con una prueba de concepto de robots RPA+IA\n\nEstimado [Nombre del jefe],\n\nMe dirijo a usted para presentarle una propuesta para aumentar la eficiencia de nuestra organizacin mediante la implementacin de robots RPA+IA. Entiendo que puede haber algunas dudas sobre cmo los robots pueden ser efectivos en nuestra rea de trabajo, pero creo que una prueba de concepto nos permitir experimentar de primera mano los beneficios que pueden aportar a nuestra empresa.\n\nPara realizar esta prueba de concepto, he contactado a un desarrollador externo experto en la implementacin de robots RPA+IA en empresas de nuestro sector. l trabajar con nosotros para demostrar cmo los robots pueden ser beneficiosos en nuestra empresa, especialmente en tareas repetitivas y de baja complejidad.\n\nSi est interesado en esta propuesta, por favor hgamelo saber para programar una reunin donde podamos discutir los detalles y establecer un plan de accin.\n\nAgradezco su consideracin y espero su respuesta.\n\nSaludos cordiales,\n\n[Nombre del remitente]",
    mostrarMas: false,
  },
];

function CopyJefe({ setShowCopy }) {
  const [copies, setCopies] = useState(correos);
  const [eliminarResto, setEliminarResto] = useState(false);
  const verMas = (id) => {
    const copia = copies.map((copy) => {
      if (copy.id === id) {
        setEliminarResto(!copy.mostrarMas);
        copy.mostrarMas = !copy.mostrarMas;
      }
      return copy;
    });
    setCopies(copia);
  };
  const apagarTodos = () => {
    const copia = copies.map((copy) => {
      copy.mostrarMas = false;
      return copy;
    });
    setCopies(copia);
    setEliminarResto(false);
  };
  return (
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0 }}
      className="copy-jefe-container"
    >
      <motion.div
        initial={{ opacity: 0, y: -10 }}
        animate={{ opacity: 1, y: 0 }}
        exit={{ opacity: 0, y: -10 }}
        className="copy-jefe"
      >
        <FontAwesomeIcon
          onClick={() => {
            setShowCopy(false);
            apagarTodos();
          }}
          icon={faCircleXmark}
          className="icon-cerrar"
        />
        <div className="container-opciones-pruebas">
          {copies.map((copy) => {
            const copySplit = copy.text.split("\n\n");
            if (!eliminarResto || copy.mostrarMas) {
              return (
                /* Correo con botones */
                <div className="copy-jefe__item" key={copy.id}>
                  {/* Correo sin botones */}
                  <div
                    className="container-copy-jefe"
                    style={{
                      maxHeight: copy.mostrarMas && "410px",
                      overflow: copy.mostrarMas && "auto",
                    }}
                  >
                    {/* Efecto degradado */}
                    <div
                      className="degradado"
                      style={{ display: copy.mostrarMas && "none" }}
                    ></div>
                    <p className="copy-jefe__text">
                      {copySplit.map((text, index) => {
                        let sentence = "sentence" + index;
                        return (
                          <span
                            key={sentence}
                            style={{
                              fontWeight: index === 0 && "600",
                            }}
                          >
                            {text}
                          </span>
                        );
                      })}
                    </p>
                  </div>

                  <div className="botones-prueba-de-concepto">
                    <button
                      className="btn-ver-mas"
                      onClick={() => verMas(copy.id)}
                    >
                      {copy.mostrarMas ? "Mostrar menos" : "Ver ms"}
                    </button>
                    <CopyToClipboard text={copy.text}>
                      <button
                        onClick={() => toast.success("Copiado en portapapeles")}
                        className="btn-copy-to-clipboard"
                      >
                        Copiar al portapapeles
                      </button>
                    </CopyToClipboard>
                  </div>
                </div>
              );
            }
            return "";
          })}
        </div>
      </motion.div>
    </motion.div>
  );
}

export default CopyJefe;
