function Step5({ nombreEmpresa, nombrePersona }) {
  const [showMore, setShowMore] = useState([false, false, false]);
  const [popUp, setPopUp] = useState(false);
  const [messagePopUp, setMessagePopUp] = useState("");
  return (
    <div className="implementar-container">
      <div className="implementar">
        <div className="first-section">
          <h3>#4 Enva tu proyecto</h3>
          <p>
            Ahora s <strong>{nombrePersona}</strong>, como paso final tendrs
            que enviar tu proyecto a <strong>{nombreEmpresa}</strong> para que
            lo evalen y puedas comenzar a trabajar. En la parte inferior, te
            dejamos unos mensajes para que puedas{" "}
            <strong>copiar dndole click al mensaje</strong>. Mucha suerte!
          </p>
          <div className="group-mensajes-personalizados">
            <Mensajes
              active={0}
              setPopUp={setPopUp}
              setMessagePopUp={setMessagePopUp}
              showMore={showMore}
              setShowMore={setShowMore}
              persona="Para los Miembros de tu equipo"
              copies={copies1}
            />
            <Mensajes
              active={1}
              setPopUp={setPopUp}
              setMessagePopUp={setMessagePopUp}
              showMore={showMore}
              setShowMore={setShowMore}
              persona="Para el Jefe"
              copies={copies2}
            />
            <Mensajes
              active={2}
              setPopUp={setPopUp}
              setMessagePopUp={setMessagePopUp}
              showMore={showMore}
              setShowMore={setShowMore}
              persona="Para el Cliente"
              copies={copies3}
            />
          </div>
        </div>
        <div className="second-section">
          <img src={quintaSeccion} alt="implementacin" />
        </div>
      </div>
      <AnimatePresence>
        {popUp && <Correos setPopUp={setPopUp} messagePopUp={messagePopUp} />}
      </AnimatePresence>
    </div>
  );
}

export default Step5;
